.home-section {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  position: relative;
  background-color: #d2e0f0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.left-sidebar {
  position: relative;
  background-color: #fff;
  max-width: 270px;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  border-radius: 0 20px 20px 0px;
}
.left-sidebar-logoBx {
  width: 100px;
  height: 50px;
  margin: 10px;
}
.left-sidebar-menuBx {
  position: relative;
  width: 100%;
  height: auto;
  display: flex;
  align-items: start;
  justify-content: center;
  flex-direction: column;
}
.menuBx-item {
  width: 100%;
  padding: 10px;
  padding-left: 40px;
  display: flex;
  align-items: center;
  margin: 10px auto;
  color: #8a8a8a;
}
.menuBx-item.active {
  border-left: 5px solid #0077ff;
  color: #0077ff;
}
.menuBx-item.active .menuBx-item-links {
  color: #0077ff;
}
.menuBx-item-links {
  color: #8a8a8a;
  margin: 0 10px;
  font-weight: bold;
}
.message-counter {
  height: 25px;
  width: 25px;
  border-radius: 50%;
  background-color: #0077ff;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}
.logoutBx {
  color: crimson;
}
.logoutBx .menuBx-item-links {
  color: crimson;
}
.middle-container {
  width: calc(100% - 380px);
  display: block;
  min-height: 100vh;
  padding: 20px 10px;
}
.middle-toogles {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.serachBx {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 400px;
  background-color: #fff;
  border-radius: 50px;
  overflow: hidden;
  padding: 0 10px;
  margin-bottom: 10px;
}
.serachBx span {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 5px;
  color: #8a8a8a;
}
.serachBx input {
  width: 100%;
  padding: 10px;
  border: none;
  outline: none;
  background-color: transparent;
  color: #8a8a8a;
}
.main-content {
  width: 100%;
  height: calc(100vh - 100px);
  /* background-color: #0077ff; */
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: auto;
  padding: 20px 0;
}
.main-content::-webkit-scrollbar {
  width: 5px;
  background-color: #eeeeee;
}

.main-content::-webkit-scrollbar-thumb {
  background: #0077ff;
  border-radius: 5px;
}
.main {
  background-color: #fff;
  width: 30%;
  min-width: 270px;
  height: 47%;
  border-radius: 20px;
  padding: 10px 10px;
}
.large {
  padding: 10px 20px;
  width: 60%;
  min-width: 430px;
}

.chart-header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.chart-header select {
  border: none;
  color: #8a8a8a;
  outline: none;
  transition: 300ms ease;
  font-weight: 600;
}
.chart-header select:hover {
  color: #0077ff;
}
.compilation-header {
  width: 100%;
  font-size: 15px;
}

.compilation-content {
  width: 100%;
  height: calc(100% - 30px);
  /* background-color: #0077ff; */
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: column;
  overflow-y: auto;
}
.compilation-content::-webkit-scrollbar {
  width: 5px;
  background-color: #eeeeee;
}

.compilation-content::-webkit-scrollbar-thumb {
  background: #0077ff;
  border-radius: 5px;
}
.compilation-item {
  width: 100%;
  height: auto;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 5px 0;
}
.text-side h3 {
  font-size: 15px;
  font-weight: bold;
}
.text-side p {
  font-size: 10px;
  color: #8a8a8a;
  font-weight: bold;
}
.progressBx {
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: conic-gradient(#007bff 0% 10%, #e0e0e0 75% 100%);
  display: flex;
  align-items: center;
  justify-content: center;
}
.progressBx::before {
  content: "";
  position: absolute;
  width: 80%;
  height: 80%;

  background-color: #fff;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}
.progressBx h3 {
  position: relative;
  z-index: 3;
  font-size: 10px;
  color: #333;
  font-weight: bold;
}

.toppers-item {
  width: 90%;
  min-height: 70px;
  height: auto;
  margin: 5px 0;
  display: flex;
  align-items: center;
  justify-content: start;
  padding: 12px 11px;
  background-color: #f0f7ff;
  border-radius: 20px;
}
.toppers-item:nth-child(1) {
  background: url("../images/first.png");
  background-size: cover;
  background-position: center;
}
.toppers-item:nth-child(2) {
  background: url("../images/second.png");
  background-size: cover;
  background-position: center;
}
.toppers-item:nth-child(3) {
  background: url("../images/third.png");
  background-size: cover;
  background-position: center;
}
.toppers-item:nth-child(1) .text-side h3,
.toppers-item:nth-child(2) .text-side h3,
.toppers-item:nth-child(3) .text-side h3 {
  color: #fff;
}
.toppers-item:nth-child(1) .text-side p,
.toppers-item:nth-child(2) .text-side p,
.toppers-item:nth-child(3) .text-side p {
  color: #fff;
}
.toppers-imgBx {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
  background-color: orange;
  margin-right: 15px;
}

.right-sidebar {
  width: 380px;
  height: 100vh;
  background-color: #fff;
}
.chart-header a {
  color: #007bff;
  font-size: 13px;
  font-weight: bold;
}

.messages-box {
  padding: 10px;

  border-radius: 8px;
  width: 100%;
  height: 90%;
  margin: 5px 0;
  overflow-y: auto;
}

.messages-list {
  display: flex;
  flex-direction: column;
}

.message-item {
  display: flex;
  align-items: start;
  border-top: 3px solid #8a8a8a2f;
  padding: 10px 0;
}

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  margin-right: 10px;
}

.message-content {
  flex: 1;
}

.message-header {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  font-weight: bold;
}

p {
  margin: 5px 0;
  font-size: 14px;
  color: #555;
}

.attachments {
  display: flex;
  gap: 5px;
  margin-top: 5px;
}

.attachment {
  font-size: 12px;
  color: #007bff;
  text-decoration: none;
  border: 2px solid #0077ff;
  padding: 5px 10px;
  border-radius: 20px;
  transition: 300ms ease;
  font-weight: bold;
}

.attachment:hover {
  background-color: #0077ff;
  color: #fff;
}

.events-header{
  font-size: 20px;
  color: #333;
  font-weight: bold;
  width: 90%;
  margin-top: 20px;
}
@media (max-width: 850px) {
  .main {
    width: 100%;
    min-width: auto;
  }
  .large {
    min-width: auto;
    width: 100%;
  }
}








