/* Calendar.css */
.calendar-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 300px;
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 8px;
  }
  
  .calendar-header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 10px;
  }
  .calendar-header button{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border:none;
    outline: none;
    color: #007bff;
    font-weight: bold;
}

  .calendar-header h2 {
    margin: 0;
    font-size: 1.2rem;
    color: #007bff;
  }
  
  .calendar-days {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
  
  .calendar-day {
    width: calc(100% / 7);
    text-align: center;
    color: #007bff;
    
    font-weight: bold;
  }
  
  .calendar-grid {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  
  .calendar-cell {
    width: calc(100% / 7);
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
   

  }
  
  .right-sidebar{
    padding: 20px 10px;
    display: flex;
    align-items: center;
    justify-content: start;
    flex-direction: column;
  }
  
  /* Calendar.css */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .modal-content {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    width: 300px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .modal-content > h3{
    color: #007bff;
  }
  .modal-content label{
    color: #333;
    font-size: 13px;
    font-weight: bold;

  }
  .modal-content input {
    width: 100%;
    padding: 5px 10px;
    margin: 5px 0;
    border: 1px dashed #007bff;
    border-radius: 10px;
    outline: none;
  }
  .modal-content button{
    padding: 10px;
    border-radius: 20px;
    border: none;
    outline: none;
    background-color: #007bff;
    color: #fff;
    font-weight: bold;
    letter-spacing: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    cursor: pointer;
  }
  .modal-content button:last-child{
    background-color: crimson;
  }
  