/* UpcomingActivities.css */
.upcoming-activities {
  
  width: 100%;
  height: auto;
  display: flex;
  align-items: start;
  justify-content: start;
  flex-direction: column;
  overflow-y: auto;
}
.upcoming-activities > h2 {
  font-size: 20px;
  color: #333;
  font-weight: bold;
}

.event-iteml {
  min-width: 300px;
  width: 80%;
  min-height: 100px;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: start;
  padding: 10px 20px;
  background-color: #F0F7FF;
  margin: 10px 0;
  border-radius: 20px;
}
.event-item-img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: orange;
  margin-right: 10px;
}
.event-details {
  width: auto;
}
.event-details>span{
  display: flex;
  align-items: center;
  justify-content: start;
  
}
p{
  margin:0;
}
.circle{
  width: 10px;
  height: 10px;
  background-color: #007bff;
  border-radius: 50%;
  margin: 0 5px;
}
.event-title{
  font-size: 15px;
  color: #333;
  font-weight: bold;
}
